

































import { GroupActionController } from "@/api/demeter/GroupActionController";

export default {
  name: "GroupOperationsTile",
  props: {
    groupAction: Object
  },

  data: () => ({
    loading: false as boolean
  }),

  methods: {
    execute() {
      this.loading = true;
      GroupActionController.executeAction(this.groupAction)
        .catch(err => {
          console.error("The query failed to execute.", err);
        })
        .finally(() => {
          this.loading = false;
        });
    }
  }
};
